<template>
  <transition name="fadeHeight">
    <div
      class="module component-module rounded text-module relative flex flex-wrap items-center text-gray-400 p-1"
      :class="[
        !is_linked && isValidComponent ? 'bg-gray-800' : 'bg-gray-600',
        renderDisabled ? 'border-8 border-b-0 border-gray-500' : '',
        is_linked && 'bg-gray-600',
      ]"
      :id="component.id">
      <module-settings-bar
        :customSettings="customSettings"
        :parent_array="parent_array"></module-settings-bar>

      <p
        ref="adminLabel"
        contenteditable="true"
        @blur="setDeep(component, 'options.admin_label', $el.innerText)"
        class="mx-auto my-0 py-2">
        {{ admin_label }}
      </p>

      <span class="mr-2 mb-0" :title="component.type">
        <component v-if="component" :is="icon" />
      </span>

      <component
        @moduleLinked="is_linked = $event"
        :component="component"
        :is="moduleType">
      </component>

      <span
        v-if="renderDisabled"
        class="w-full text-center py-1 text-sm italic bg-gray-500"
        >Frontend Output Disabled
      </span>
    </div>
  </transition>
</template>

<script>
import { EventBus } from '../../EventBus'
import { setDeep } from '../../functions/objectHelpers'
import { mapGetters } from 'vuex'
import {
  MenuIcon,
  MinusIcon,
  MapIcon,
  CoffeeIcon,
  PlayCircleIcon,
  ImageIcon,
  CodeIcon,
  SlidersIcon,
  ArrowRightIcon,
  AlignJustifyIcon,
  BoldIcon,
  GridIcon,
  LayoutIcon,
  LockIcon,
} from 'vue-feather-icons'

export default {
  name: 'module-base',
  components: {
    MenuIcon,
    MinusIcon,
    MapIcon,
    CoffeeIcon,
    PlayCircleIcon,
    ImageIcon,
    CodeIcon,
    SlidersIcon,
    ArrowRightIcon,
    AlignJustifyIcon,
    BoldIcon,
    GridIcon,
    LayoutIcon,
    LockIcon,
  },
  props: {
    component: Object,
    parent_array: Array,
  },
  data() {
    return {
      icon: this.component.icon,
    }
  },
  computed: {
    ...mapGetters(['validComponents']),
    is_linked() {
      return this.component.content?.acfForm?.is_linked
    },
    admin_label() {
      if (this.component.options && this.component.options.admin_label) {
        return this.component.options.admin_label
      } else {
        return this.component.type
          ? this.component.type
          : 'Placeholder for unknown component'
      }
    },
    isValidComponent() {
      return this.componentMap(this.component.type)
    },
    moduleType() {
      return this.isValidComponent ? this.component.type : 'ShellModule'
    },
    renderDisabled() {
      return this.component.attributes?.renderDisabled || false
    },
    isGlobalModule() {
      return this.component.type === 'global-module'
    },
    editPageLink() {
      return this.component.options.editPageLink?.url || false
    },
    customSettings() {
      if (this.isGlobalModule && this.editPageLink) {
        return {
          edit_page: {
            name: 'Edit Page',
            icon: 'ExternalLinkIcon',
            title: 'Go to edit page',
            action: this.goToEditPage,
            order: 31,
          },
        }
      }
      return {}
    },
  },
  methods: {
    componentMap(type) {
      return !!this.validComponents.find((el) => el.type === type)
    },
    goToEditPage() {
      window.open(this.editPageLink, '_blank')
    },
    setDeep,
  },
  created() {
    EventBus.$on('moduleLinked', (postID, isLinked) => {
      if (this.component?.content?.acfForm?.post_id == postID) {
        this.setDeep(this.component, 'content.acfForm.is_linked', isLinked)
        this.icon = isLinked ? 'LockIcon' : 'LayoutIcon'
      }
    })

    // Deal with globals that never had the new feature (backwards compat)
    if (this.isGlobalModule && !this.editPageLink) {
      this.setDeep(
        this.component,
        'options.editPageLink.url',
        `/wp-admin/post.php?post=${this.component.content.id}&action=edit`
      )
    }
  },
  destroyed() {
    EventBus.$off('moduleLinked')
  },
  provide() {
    return {
      component: this.component,
      parent_array: this.parent_array,
    }
  },
}
</script>
<style>
.component-module {
  transition: all 0.2s;
}

.list-enter-active,
.list-leave-active {
  transition: all 1s;
}
.list-enter, .list-leave-to /* .list-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translateY(30px);
}
</style>
